html {
	font-size: 16px;
	width: 100%;
	height: 100%;
	padding: 0;
	margin: 0;
}
body {
	font-family: $mainFont;
	color: $textMainColor;
	background: #F8F8F8;
	width: 100%;
	height: 100%;
	padding: 0;
	margin: 0;
	overflow-y: hidden !important;
}

*{
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	cursor: url("data:image/svg+xml,%3Csvg width='62' height='62' viewBox='0 0 62 62' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='31' cy='31' r='30' stroke='%23FF473B' stroke-width='2'/%3E%3Ccircle opacity='0.32' cx='31.5' cy='30.5' r='22.5' fill='%23FF473B'/%3E%3Ccircle cx='31.5' cy='31.5' r='3.5' fill='%23FF473B'/%3E%3C/svg%3E%0A") 31 31, pointer !important;
}

.no-event{
	pointer-events: none !important;
}

.main{
	overflow: hidden;
}

.proxy {
	position: absolute;
	visibility: hidden;
}

.slideshow {
	width: 100vw;
	height: 100%;
	display: flex;
	z-index: 10;

	&__slide {
		color: black;
		height: 100vh;
		width: 100%;
	}
}



.shapes{
	position: fixed;
	left: 0;
	top: 0;
	z-index: 15;
	width: 100%;
	height: 100vh;
	user-select: none;
	overflow: hidden;
	pointer-events: none;
}
.shape{
	position: absolute;
	left: 200%;
	top: 0;
	z-index: 0;
	height: 100vh;
	width: 100%;
	&__bg{
		position: absolute;
		left: 0;
		transform: translateX(-25%);
		top: 0;
		width: 68.75%;
		height: 100%;
		background: #EE7B09;
		clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
	}
	&__arrow{
		position: absolute;
		left: 54%;
		top: vh(261);
		z-index: 3;
		pointer-events: none;
		svg{
			display: block;
			width: vw(105);
			height: vw(31);

		}
	}
	&__number{
		position: absolute;
		z-index: 4;
		pointer-events: none;
		left: 50%;
		bottom: 4%;
		font-weight: 900;
		font-size: vw(600);
		line-height: vw(443);
		color: #FFFFFF;
		opacity: 0.04;
	}
}



.texts{
	position: fixed;
	left: 0;
	top: 0;
	z-index: 20;
	width: 100%;
	height: 100%;
	user-select: none;
	overflow: hidden;
	pointer-events: none;
}
.text{
	position: absolute;
	left: 0;
	top: 0;
	z-index: 0;
	height: 100%;
	width: 100%;
	&__title{
		font-weight: 900;
		font-size: vw(120);
		line-height: vw(96);
		position: relative;
		z-index: 3;
		padding-top: vh(217);
		margin-left: vw(552);
		span{
			color: #ffffff;
		}
	}
}



.home{
	position: fixed;
	left: 0;
	top: 0;
	z-index: 11;
	width: 100%;
	height: 100%;
	pointer-events: none;
	&__text{
		display: none;
	}

	&__overlay{
		z-index: 2;
		width: 100%;
		height: 100%;
		left: 125%;
		top: 0;
		background: #F8F8F8;
		position: absolute;
	}

	&__video{
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		z-index: 1;
		&-img{
			width: 100%;
			height: 100%;
			position: relative;
			video,
			img{
				width: 100%;
				height: 100%;
				display: block;
				object-fit: cover;
				position: absolute;
				left: 0;
				top: 0;
				z-index: 1;
			}
			canvas{
				width: 100%;
				height: 100%;
				display: block;
				position: absolute;
				left: 0;
				top: 0;
				z-index: 2;
			}
		}
		&-btn{
			text-decoration: none;
			display: none;
			position: absolute;
			height: vw(51);
			left: vw(742);
			top: vh(582);
			background: #FFFFFF;
			border-radius: vw(100);
			padding: vw(10) vw(22) vw(10) vw(55);
			z-index: 5;
			.btn-text{
				font-weight: 500;
				font-size: vw(18);
				line-height: vw(30);
				color: #272936;
			}
			.play{
				display: block;
				width: vw(60);
				height: vw(60);
				background: #FF473B url(../images/play.svg) 51% center no-repeat;
				background-size: vw(14) vw(20);
				border-radius: 100%;
				position: absolute;
				left: -20px;
				top: 50%;
				transform: translateY(-50%);
				box-shadow: 0 vw(22) vw(22) rgba(0, 0, 0, 0.25);
				transition: transform 0.1s ease;
			}
			&:hover {
				.play {
					transform: translateY(-50%) scale(1.1);
				}
			}
		}
	}
}

.about{
	&__text{
		font-size: vw(32);
		line-height: vw(52);
		margin-top: vh(64);
		margin-left: vw(669);
		width: vw(710);
		position: relative;
		> * + *{
			margin-top: 1em;
		}
	}

	&__year{
		font-weight: 300;
		font-size: vw(90);
		line-height: vw(65);
		margin-top: vh(84);
		margin-left: vw(663);
		position: relative;


		&-text{
			font-size: vw(22);
			line-height: vw(40);
			margin-top: vh(32);
			margin-left: vw(667);
			width: vw(600);
			position: relative;
			> * + *{
				margin-top: 2em;
			}
		}
	}
}


.geo{
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	pointer-events: none;
	z-index: 12;

	&__title{
		margin-left: vw(709);
	}
	&__list{
		position: absolute;
		z-index: 4;
		right: 17%;
		bottom: 10%;
		&-item{
			text-align: right;
			white-space: nowrap;
			font-style: normal;
			font-weight: bold;
			font-size: vw(18);
			line-height: 1.2;
			color: #FFFFFF;
			opacity: 0.32;
			transition: opacity 0.1s linear, font-size 0.1s linear;
			position: relative;
			pointer-events: auto;

			&:hover {
				&:not(.geo__list-item--big) {
					opacity: 0.5;
				}
			}
			&--big{
				font-size: vw(32);
				opacity: 1;
			}
			&+.geo__list-item{
				margin-top: vw(20);

			}
		}
	}
	&__map{
		position: absolute;
		left: vw(565);
		top: vh(387);
		z-index: 1;
		width: vw(793);
		height: vh(424);
		svg,
		img{
			display: block;
			width: 100%;
			height: 100%;
			object-fit: contain;
			path{
				transition: 0.1s linear;
				&.active{
					fill-opacity: 1;
					fill: #EE7B09;
				}
			}
		}

		&-wrapper{
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
		}
	}
}


.gallery{
	position: fixed;
	left: 0;
	top: 0;
	z-index: 13;
	width: 100%;
	height: 100%;
	pointer-events: none;
	&__left{
		position: absolute;
		width: vw(388);
		right: 7.4%;
		top: vw(337);
		&-navigation{
			margin-top: vh(16);
			display: flex;
			align-items: center;
			.slide-btn{
				font-size: 0;
				line-height: 0;
				display: block;
				width: vw(54);
				height: vw(54);
				padding: 0;
				color: transparent;
				border: none;
				outline: none;
				background: url(../images/arrow.svg) center center no-repeat;
				background-size: contain;
				pointer-events: auto;
				&--next{
					transform: scaleX(-1);
				}
			}
			.slide-pagination{
				margin: 0 vw(23);
				.slick-dots{
					display: flex;
					align-items: center;
					li{
						margin: 0 3px;
						button{
							font-size: 0;
							line-height: 0;
							display: block;
							width: 5px;
							height: 5px;
							padding: 0;
							color: transparent;
							border: none;
							outline: none;
							background: #C9C9C9;
							border-radius: 50%;
							transition: 0.1s linear;
						}
						&.slick-active{
							button{
								width: 9px;
								height: 9px;
								background: #EE7B09;
							}
						}
					}
				}
			}
		}
	}
	&__slider{
		width: 100%;
	}
	&__slide{
		width: vw(184);
		height: vh(131);
		margin: 0 vw(5);
		pointer-events: auto;
		img{
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		&.slick-current{
			position: relative;
			&::after{
				content: "";
				display: block;
				width: 100%;
				height: 7px;
				background-color: #EE7B09;
				position: absolute;
				left: 0;
				bottom: 0;
			}
		}
	}

	&__right{
		position: absolute;
		z-index: 1;
		right: 0;
		top: 0;
		height: 100%;
		width: 100%;
	}
	&__big-slider{
		width: 100%;
		height: 100%;
		* {
			height: 100%;
		}
	}
	&__big-slide{
		height: 100%;
		img{
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

.contacts{
	position: fixed;
	left: 0;
	top: 0;
	z-index: 14;
	width: 100%;
	height: 100%;
	pointer-events: none;

	&__content{
		pointer-events: none;
		position: relative;
	}
	&__left{
		margin-left: vw(532);
		padding-top: vh(324);
		width: vw(290);
		color: #FFFFFF;
	}
	&__title{
		font-weight: 900;
		font-size: vw(32);
		line-height: 1;
	}
	&__text{
		margin-top: vh(32);
		font-size: vw(18);
		line-height: 1.28;
	}
	&__items{
		margin-top: vh(34);
	}
	&__item{
		font-size: vw(18);
		line-height: 1.28;
		a{
			text-decoration: none;
			pointer-events: auto;
			color: #FFFFFF;
		}
		+.contacts__item{
			margin-top: vh(14);
		}
	}


	&__right{
		position: absolute;
		z-index: 1;
		right: 0;
		top: 0;
		height: 100%;
		width: vw(1130);
	}
	&__map{
		width: 100%;
		height: 100%;
		iframe{
			width: 100%;
			height: 100%;
		}
	}
}