.header{
	height: vh(100);
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	display: flex;
	align-items: center;
	z-index: 100;
	transition: background-color 0.3s ease;
	user-select: none;


	&__container{
		margin: 0 auto;
		padding-left: vw(273);
		padding-right: vw(128);
		display: flex;
		//max-width: 1664px;
		box-sizing: border-box;
		align-items: center;
		width: 100%;
		justify-content: space-between;
	}
	&__logo{
		margin-right: vw(147);
		a,div{
			width: vw(105);
			display: block;
			svg{
				display: block;
				width: 100% !important;
				height: auto !important;
				path{
					transition: fill 0.3s ease;
				}
			}
		}
	}
	&__menu{
		display: flex;
		align-items: center;
		flex-grow: 1;
		&-item{
			margin: 0 vw(30);
			a{
				font-weight: 500;
				font-size: vw(16);
				line-height: vw(19);
				letter-spacing: -0.015em;
				text-decoration: none;
				color: #272936;
				transition: color 0.3s ease;
				&:hover{
					text-decoration: underline;
				}
			}

			&:nth-child(1){
				margin-right: auto;
			}
		}
	}
	&__btn{
		margin-left: vw(20);
		a{
			font-weight: bold;
			font-size: vw(16);
			line-height: 1.2;
			text-align: center;
			color: #FFFFFF;
			display: flex;
			align-items: center;
			justify-content: center;
			background: #FF473B;
			border-radius: vw(26);
			box-shadow: 0 vw(12) vw(12) rgba(0, 0, 0, 0.07);
			transition: box-shadow 0.3s ease, background-color 0.3s ease;
			width: vw(224);
			height: vw(48);
			text-decoration: none;
			&:hover{
				box-shadow: 0 vw(12) vw(12) rgba(0, 0, 0, 0.3);
			}
		}
	}

	&__right{
		display: flex;
		align-items: center;
		flex-grow: 1;
	}

	&__burger{
		display: none;
	}


	&.orange{
		background: #EE7B09;
		.header__btn{
			a{
				background-color: #272936;
			}
		}
	}
	&.light{
		.header__logo{
			svg{
				path{
					fill: #ffffff;
				}
			}
		}

	}
	&.menu1{
		.header__menu{
			&-item{
				&:nth-child(1){
					a{
						color: #FFFFFF;
					}
				}
			}
		}
	}
	&.menu3{
		.header__menu{
			&-item{
				a{
					color: #FFFFFF;
				}
			}
		}
	}
}


