.checkbox {
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 56px;
  cursor: pointer;
  user-select: none;

  input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
  }

  &__checkmark {
    position: absolute;
    top: 3px;
    left: 0;
    width: 42px;
    height: 20px;
    background: #FFFFFF;
    border: 1px solid #FF473B;
    border-radius: 100px;
    transition: 0.4s linear;

    &::after {
      content: "";
      position: absolute;
      top: -4px;
      left: -2px;
      width: 26px;
      height: 26px;
      border-radius: 50%;
      border: 1px solid #FF473B;
      background-color: #ff8b82;
      transition: 0.3s ease-in-out;
    }
  }

  &__text {
    @include max("sm") {
      font-size: 14px;
      line-height: 15px;
    }
  }

  & input:checked ~ .checkbox__checkmark {
    background-color: #ff8b82;
    &::after {
      transform: translateX(18px);
      background-color: #FF473B;
    }
  }
}

.input {
  font-size: 14px;
  line-height: 15px;
  letter-spacing: -0.2px;

  &-item {
    width: 100%;
    padding: 29px 0;
    border: none;
    border-bottom: 3px solid rgba(166, 180, 191, 0.32);

    @include max("sm") {
      padding: 15px 0;
    }

    &:focus {
      outline: none;
      border-color: #272936;
    }

    &--textarea {
      resize: vertical;
      min-height: 76px;
      max-height: 211px;

      @include max("sm") {
        resize: none;
        min-height: 75px;
      }
    }
  }
}

.upload {
  position: relative;
  display: flex;
  align-items: center;
  height: 54px;
  cursor: pointer;

  &:hover{
    .upload {
      &__text {
        color: #19162c;
      }

      &__icon {
        circle {
          stroke: #19162c;
        }

        path {
          fill: #19162c;
        }
      }
    }
  }


  &__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    opacity: 0;
    visibility: hidden;
  }

  &__text {
    margin-right: 16px;
    font-weight: 700;
    font-size: 16px;
    line-height: 26px;
    color: #a6b4bf;
    transition: 0.3s ease-in-out;
  }

  &__icon {
    width: 54px;
    height: 54px;

    circle,
    path {
      transition: 0.3s ease-in-out;
    }
  }
}


.button {
  padding: 11px 28px;
  font-weight: bold;
  font-size: 16px;
  line-height: 26px;
  text-align: center;
  text-decoration: none;
  border: none;
  border-radius: 25.5px;
  color: #FFFFFF;
  background-color: #FF473B;
  filter: drop-shadow(0px 12px 12px rgba(0, 0, 0, 0.07));
  cursor: pointer;
  transition: 0.3s ease-in-out;

  &:hover{
    background-color: #ff8f2b;
  }


  min-width: 227px;

  @include max("md") {
    align-self: flex-end;
    margin-top: 20px;
  }

  @include max("sm") {
    align-self: center;
  }
}



.form {
  padding: 44px 32px 32px;
  border-radius: 12px;
  background-color: #FFFFFF;

  @include max("sm") {
    padding: 30px 15px;
  }

  &__title {
    margin-bottom: 36px;
    font-weight: 700;
    font-size: 24px;
    line-height: 24px;
    letter-spacing: -0.015em;
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }

  &__input {
    margin-bottom: 20px;
    width: calc(50% - 15px);

    @include max("sm") {
      width: calc(50% - 10px);
    }

    @include max("xs") {
      width: 100%;
    }

    &:nth-child(2n) {
      margin-left: 30px;

      @include max("sm") {
        margin-left: 20px;
      }

      @include max("xs") {
        margin-left: 0;
      }
    }
  }

  &__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 26px;
    background: radial-gradient(48.31% 215.81% at 50% 50%, #272936 0%, #19162c 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &__extra {
    align-items: center;
    margin-bottom: 12px;

    @include max("sm") {
      flex-direction: column-reverse;
    }

    &-file {
      margin-left: auto;
      margin-bottom: 20px;
    }

    &-text {
      @include max("sm") {
        width: 100%;
      }
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include max("md") {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__button {
    min-width: 227px;

    @include max("md") {
      align-self: flex-end;
      margin-top: 20px;
    }

    @include max("sm") {
      align-self: center;
    }
  }
}




.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 120;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: 0.3s ease-in-out;



  &.active {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }

  &__overlay {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 121;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    opacity: 0.5;
    cursor: pointer;
  }

  &__content {
    position: fixed;
    top: 50%;
    left: 50%;
    z-index: 122;
    transform: translate(-50%, -50%);
    max-width: 818px;
    width: 100%;

    @include max("md") {
      width: 90%;
    }

    @media (max-height: 590px) {
      height: 90vh;
      overflow: auto;
    }

    &-close {
      position: absolute;
      top: 25px;
      right: 17px;
      padding: 15px;
      font-size: 24px;
      line-height: 24px;
      cursor: pointer;
      transition: 0.3s ease-in-out;

      &:hover {
        color: #FF473B;
      }


      @include max("sm") {
        top: 12px;
        right: 0;
      }
    }
  }

  ::-webkit-scrollbar {
    display: none;
  }
}
